#vis-main {
  width: 100vw;
  height: 100vh;
  padding: 16px 8px 16px 16px;
  font-size: 14px;
}
#vis-main .ant-tree {
  width: 100%;
  height: 200px;
  overflow-y: auto;
  padding-top: 8px;
}
#vis-main .ant-tree .ant-tree-switcher {
  width: 0;
  display: none;
}
#vis-main .ant-tree .ant-tree-node-content-wrapper {
  width: 100%;
}
#vis-main .box {
  height: auto;
  border: 1px solid #666;
  margin: 16px 0;
  position: relative;
  padding: 16px 0;
}
#vis-main .box .box-title {
  position: absolute;
  top: -14px;
  left: 20px;
  background-color: #fff;
  padding: 0 5px;
  font-weight: bold;
  font-style: italic;
}
#vis-main .box .box-content {
  display: flex;
  font-size: 14px;
  padding: 0 8px;
}
#vis-main .box .box-content .column {
  width: 50%;
}
#vis-main .box .box-content .column:first-child {
  padding-right: 8px;
}
#vis-main .box .box-content .column:last-child {
  padding-left: 8px;
}
#vis-main .box .box-content .column label {
  width: 70px;
  text-align: right;
  line-height: 32px;
  padding-right: 5px;
}
#vis-main .box .box-content .column label::after {
  content: ":";
}
#vis-main .box .box-content .column .ant-input {
  width: calc(100% - 70px);
}
#vis-main .box .box-content .column .row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}
#vis-main .box .box-content .ant-radio-group {
  width: 100%;
}
#vis-main .chart-container {
  width: 100%;
  height: 33.33333333%;
  position: relative;
}
#vis-main .chart-container .chart,
#vis-main .chart-container .loading_mask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
#vis-main .chart-container .loading_mask {
  background: rgba(255, 255, 255, 0.6);
  display: none;
  align-items: center;
  justify-content: center;
}
#vis-main .chart-container:before {
  position: absolute;
  top: -10px;
  width: 100%;
  height: 30px;
  text-align: center;
  line-height: 30px;
  z-index: 999;
  font-weight: bold;
  font-size: 16px;
}
#vis-main .chart-container:first-child:before {
  content: "A->B";
}
#vis-main .chart-container:nth-child(2):before {
  content: "B->A";
}
#vis-main .chart-container:last-child:before {
  content: "SYM";
}
#vis-main .help-mask {
  width: 100vw;
  height: 100vh;
  padding: 8px;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 9999;
  display: flex;
}
#vis-main .help-mask::before {
  width: 100%;
  height: 80px;
  line-height: 80px;
  position: absolute;
  text-align: center;
  color: #fff;
  font-size: 34px;
  content: "点击任意处关闭";
  font-weight: bold;
}
#vis-main .help-mask .mask-tip {
  position: absolute;
}
#vis-main .help-mask .column {
  height: 100%;
}
#vis-main .help-mask .column .dot-box {
  width: 100%;
  border: 2px dashed transparent;
  border-radius: 4px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  margin-bottom: 2px;
}
#vis-main .help-mask .column:nth-child(1) {
  width: 316px;
  padding: 12px 3px 0;
}
#vis-main .help-mask .column:nth-child(1) .dot-box:nth-child(1) {
  height: 220px;
  border-color: orangered;
  color: orangered;
}
#vis-main .help-mask .column:nth-child(1) .dot-box:nth-child(2) {
  height: 200px;
  border-color: orange;
  color: orange;
}
#vis-main .help-mask .column:nth-child(1) .dot-box:nth-child(3) {
  height: 70px;
  border-color: chocolate;
  color: chocolate;
}
#vis-main .help-mask .column:nth-child(1) .dot-box:nth-child(4) {
  height: 80px;
  border-color: coral;
  color: coral;
}
#vis-main .help-mask .column:nth-child(2) {
  width: calc((100% - 316px) / 3 * 2);
  padding-right: 3px;
}
#vis-main .help-mask .column:nth-child(2) .dot-box {
  height: calc((100% - 20px)/3);
}
#vis-main .help-mask .column:nth-child(2) .dot-box:nth-child(1) {
  border-color: blue;
  color: blue;
}
#vis-main .help-mask .column:nth-child(2) .dot-box:nth-child(2) {
  border-color: cornflowerblue;
  color: cornflowerblue;
}
#vis-main .help-mask .column:nth-child(2) .dot-box:nth-child(3) {
  height: calc(100% / 3 + 4px);
  border-color: #48aad1;
  color: #48aad1;
}
#vis-main .help-mask .column:nth-child(3) {
  width: calc((100% - 316px) / 3);
}
#vis-main .help-mask .column:nth-child(3) .dot-box {
  height: calc(100% - (1px / 3) - 5px);
  border-color: limegreen;
  color: limegreen;
}
